  font-size: 10px;
  font-weight: 400;
  line-height: 28px;
  text-transform: uppercase;
  letter-spacing: 0.25px;
}
.blog__main--block1MoreHeartSpan {
  color: #000000;
  font-size: 12px;
  font-weight: 700;
  line-height: 28px;
  padding-left: 5px;
  text-transform: uppercase;
