  color: #898989;
  font-size: 10px;
  font-weight: 400;
  line-height: 19px;
  padding-left: 35px;
  letter-spacing: 1.59px;
}
.blog__main--blockMoreText {
  color: #000000;
  font-size: 14px;
  font-weight: 400;
