}
.blog__main--block3More {
  width: 345px;
  height: 296px;
  background-color: #f6f6ef;
  margin-bottom: 45px;
  overflow: hidden;
  display: inline-block;
}
.blog__main--block3More-2 {
  width: 345px;
  height: 362px;
  background-color: #f6f6ef;
  overflow: hidden;
}
.blog__more {
  text-align: right;
  margin-right: 10px;
}
.blog__more a {
  color: #000000;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  text-decoration: none;
  letter-spacing: 1.6px;
  text-transform: uppercase;
}
.blog__more a:hover {
  border-bottom: 2px solid #000;
  transition: 0.2s;
}
.blog__more span {
  margin-left: 20px;
}
/* Footer */
.footer {
  height: 620px;
  color: #fff;
  background-color: #171515;
}
.footer__container {
  margin: 0 auto;
  max-width: 1032px;
  padding: 0 20px;
}
.footer__contact {
  display: flex;
  justify-content: space-between;
  padding: 100px 0 0 0;
}
.footer__contact--text h2 {
  color: #ffffff;
  font-size: 30px;
  font-weight: 400;
  line-height: 28px;
  text-transform: uppercase;
}
.footer__contact--text p {
  width: 400px;
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0.35px;
}
.footer__contact--email i {
  font-size: 28px;
  color: #898989;
}
.footer__contact--email span {
  color: #ffffff;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0.8px;
  padding-left: 20px;
}
.footer__contact--tel i {
  font-size: 28px;
  color: #fff;
}
.footer__contact--tel span {
  color: #ffffff;
  padding-left: 20px;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0.8px;
}
.footer__contact--link {
  color: #ffffff;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  text-decoration: none;
  letter-spacing: 1.6px;
}
.footer__contact--link:hover {
  text-decoration: underline;
}
.footer__form--message {
  position: relative;
}
.footer__form--message label {
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0.35px;
  position: absolute;
  left: 20px;
  top: 0;
}
.footer__form--name {
  position: relative;
}
.footer__form--name label {
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0.35px;
  position: absolute;
  left: 20px;
  top: 0;
}
.footer__form--subject {
  position: relative;
}
.footer__form--subject label {
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0.35px;
  position: absolute;
  left: 20px;
  top: 40px;
}
.footer__form--main {
  display: flex;
  justify-content: space-between;
  padding-top: 80px;
}
.footer__send {
  color: #ffffff;
  font-family: Lato;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
  line-height: 28px;
  background: transparent;
  border: none;
  margin: 40px auto;
  display: block;
  text-transform: uppercase;
  letter-spacing: 1.6px;
}
.footer__send:hover {
  letter-spacing: 5px;
  border-bottom: 2px solid #fff;
  transition: 0.5s;
}
#message {
  width: 600px;
  background: transparent;
  border: none;
  padding: 140px 0 10px 0;
  border-bottom: 1px solid #fff;
  outline: none;
  color: #fff;
  font-size: 20px;
}
#name {
  width: 210px;
  background: transparent;
  border: none;
  padding: 40px 0 0 0;
  border-bottom: 1px solid #fff;
  outline: none;
  color: #fff;
  font-size: 20px;
}
#subject {
  width: 210px;
  background: transparent;
  border: none;
  padding: 75px 0 10px 0;
  border-bottom: 1px solid #fff;
  outline: none;
  color: #fff;
  font-size: 20px;
}

